import React, { useEffect } from 'react'
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from 'react-icons/md'
import { QuestionAnswersStore } from '../../../../actions'
import { TrueFalseQuestionAnswerInterface, questionTypeElementsType } from '../../../../interfaces/question'

interface TrueFalseProps {
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null, 
    answers: TrueFalseQuestionAnswerInterface[] | undefined,
  };
  answerQuestionFn: Function;
}

const TrueFalse = (props: TrueFalseProps) => {
  const checkAnswer = (item: TrueFalseQuestionAnswerInterface): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(itm => itm.question_id === props.selectedQuestionAnswer.question_id);
      if (check !== undefined) {
        let x = check.answer.find(itm => itm.id === item.id);
        if (x !== undefined && x.status === true) {
          res = true;
        }
      }
    }
    return res;
  }
  const addAnswerItemFn = (newAnswers: TrueFalseQuestionAnswerInterface[]) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.answerQuestionFn({
        question_id: props.selectedQuestionAnswer.question_id,
        status: false,
        answer: newAnswers
      });
    }
  }

  const addAnswer = (item: TrueFalseQuestionAnswerInterface, question_id: string | null) => {
    let oldAnswers = props.available_answers.find(itm => itm.question_id === question_id);
    if (oldAnswers !== undefined) {
      // Check type of question
      let temp: TrueFalseQuestionAnswerInterface[] = [];
      for (let t of oldAnswers.answer) {
        temp = [...temp, {
          id: t.id,
          value: t.value,
          status: false,
        }]
      }
      addAnswerItemFn(temp);
      let newAnswers: TrueFalseQuestionAnswerInterface[] = [];
      for (let x of temp.length === 0 ? oldAnswers.answer : temp) {
        if (x.id === item.id) {
          newAnswers = [...newAnswers, {
            id: item.id,
            value: item.value,
            status: !x.status,
          }]
        } else {
          newAnswers = [...newAnswers, x]
        }
      }
      addAnswerItemFn(newAnswers);
    }
  }

  useEffect(() => {
    let test = props.available_answers.find(itm => itm.question_id === props.selectedQuestionAnswer.question_id);
    if (test === undefined || (test !== undefined && test.answer.length === 0)) {
      if (props.selectedQuestionAnswer.answers !== undefined && props.selectedQuestionAnswer !== undefined &&  props.selectedQuestionAnswer.question_id !== null) {
        let newAnswers: TrueFalseQuestionAnswerInterface[] = [];
        for (let a of  props.selectedQuestionAnswer.answers) {
          newAnswers = [...newAnswers, {
            id: a.id,
            value: a.value,
            status: false
          }]
        }
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: true,
          answer: newAnswers
        });
      }
    }
  }, [props])
  
  return (
    <div className="">
      {props.selectedQuestionAnswer !== null &&
      props.selectedQuestionAnswer.answers !== undefined && props.selectedQuestionAnswer.answers.map((item) => (
        <div onClick={() => addAnswer(item, props.selectedQuestionAnswer.question_id)} className={`px-2 py-2 border-b ${checkAnswer(item) === true ? "border bg-blue-100 border-white text-blue-600 hover:border-blue-600 rounded font-bold" : "border-b bg-white border-gray-100 hover:bg-gray-100"} flex flex-row justify-left items-center cursor-pointer`}>
          {checkAnswer(item) === true ?
          <MdRadioButtonChecked className="text-2xl" />:
          <MdRadioButtonUnchecked className="text-2xl" />}
          <span className="ml-2">{item.value}</span>
        </div>
      ))}
    </div>
  )
}

export default TrueFalse
