import React, { useEffect } from 'react'
import { ImCheckboxChecked, ImCheckboxUnchecked } from 'react-icons/im'
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from 'react-icons/md'
import { QuestionAnswersStore } from '../../../../actions'
import { MultipleChoiceQuestionAnswerInterface, questionTypeElementsType } from '../../../../interfaces/question'

interface MultipleChoiceProps {
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null, 
    answers: MultipleChoiceQuestionAnswerInterface[] | undefined,
  };
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
}

const MultipleChoice = (props: MultipleChoiceProps) => {
  
  const checkAnswer = (item: MultipleChoiceQuestionAnswerInterface): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(itm => itm.question_id === props.selectedQuestionAnswer.question_id);
      if (check !== undefined) {
        let x = check.answer.find(itm => itm.id === item.id);
        if (x !== undefined && x.status === true) {
          res = true;
        }
      }
    }
    return res;
  }

  const addAnswerItemFn = (newAnswers: MultipleChoiceQuestionAnswerInterface[]) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.answerQuestionFn({
        question_id: props.selectedQuestionAnswer.question_id,
        status: false,
        answer: newAnswers
      });
    }
  }
  const addAnswer = (item: MultipleChoiceQuestionAnswerInterface, question_id: string | null) => {
    let oldAnswers = props.available_answers.find(itm => itm.question_id === question_id);
    if (oldAnswers !== undefined) {
      // Check type of question
      let temp: MultipleChoiceQuestionAnswerInterface[] = [];
      if (props.type === questionTypeElementsType.SingleMultiple) {
        for (let t of oldAnswers.answer) {
          temp = [...temp, {
            id: t.id,
            value: t.value,
            status: false,
          }]
        }
        addAnswerItemFn(temp);
      }
      let newAnswers: MultipleChoiceQuestionAnswerInterface[] = [];
      for (let x of temp.length === 0 ? oldAnswers.answer : temp) {
        if (x.id === item.id) {
          newAnswers = [...newAnswers, {
            id: item.id,
            value: item.value,
            status: props.type === questionTypeElementsType.SingleMultiple ? true : !x.status,
          }]
        } else {
          newAnswers = [...newAnswers, x]
        }
      }
      addAnswerItemFn(newAnswers);
    }
  }
  useEffect(() => {
    let test = props.available_answers.find(itm => itm.question_id === props.selectedQuestionAnswer.question_id);
    if (test === undefined || (test !== undefined && test.answer.length === 0)) {
      if (props.selectedQuestionAnswer.answers !== undefined && props.selectedQuestionAnswer !== undefined &&  props.selectedQuestionAnswer.question_id !== null) {
        let newAnswers: MultipleChoiceQuestionAnswerInterface[] = [];
        for (let a of  props.selectedQuestionAnswer.answers) {
          newAnswers = [...newAnswers, {
            id: a.id,
            value: a.value,
            status: false
          }]
        }
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: true,
          answer: newAnswers
        });
      }
    }
  }, [props])
  return (
    <div>
      {props.selectedQuestionAnswer !== null &&
      props.selectedQuestionAnswer.answers !== undefined && props.selectedQuestionAnswer.answers.map((item) => (
        <div onClick={() => addAnswer(item, props.selectedQuestionAnswer.question_id)} className={`px-2 py-2 ${checkAnswer(item) === true ? "border bg-blue-100 border-white text-blue-600 hover:border-blue-600 rounded font-bold" : "border-b bg-white border-gray-100 hover:bg-gray-100"} flex flex-row justify-left items-center cursor-pointer`}>
          {checkAnswer(item) === true ?
          <>
          {props.type === questionTypeElementsType.SingleMultiple ? 
          <MdRadioButtonChecked className="text-2xl" /> : 
          <ImCheckboxChecked className="text-2xl" />}
          </>:
          props.type === questionTypeElementsType.SingleMultiple ? 
          <MdRadioButtonUnchecked className="text-2xl truncate" />:
          <ImCheckboxUnchecked className="text-2xl" />}
          <span className="ml-2">{item.value}</span>
        </div>
      ))}
    </div>
  )
}

export default MultipleChoice
